.snake-game {
  width: 100%;
  height: 100%;

  display: flex;
  flex-direction: column;

  &__board {
    flex-grow: 1;

    display: flex;
    flex-direction: column;

    align-items: center;
    justify-content: center;

    overflow: hidden;
  }

  &__svg {
    rect.wall {
      fill: #307916;
    }
  }
}
