.paused {
  position: fixed;

  width: 100%;
  height: 100%;

  &__card {
    position: fixed;

    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  &__backdrop {
    width: 100%;
    height: 100%;

    background-color: black;
    opacity: 0.2;
  }

  &__title {
    font-size: 4rem;

    color: white;

    text-align: center;

    margin: 15px;
  }
}
