@import url(https://fonts.googleapis.com/css?family=VT323);
@import '../../assets/mixins.scss';

.menu-link {
  @include app-button;

  &--primary {
    background: #005800;

    &:focus,
    &:hover {
      background: #006800;
    }
  }

  &--secondary {
    background: #0000bc;

    &:focus,
    &:hover {
      background: #0000fc;
    }
  }
}
