@import url(https://fonts.googleapis.com/css?family=VT323);
@import url(https://fonts.googleapis.com/css?family=VT323);

.menu-button{position:relative;display:inline-block;background:#7c7c7c;color:white;border-bottom:6px inset rgba(0,0,0,0.5);border-left:6px inset rgba(0,0,0,0.5);border-right:6px inset rgba(255,255,255,0.5);border-top:6px inset rgba(255,255,255,0.5);margin:1rem;min-width:200px;padding:1rem 4.6rem;cursor:pointer;font-size:2.4rem;text-decoration:none;text-align:center;text-transform:uppercase;box-shadow:0 3px 6px rgba(0,0,0,0.16),0 3px 6px rgba(0,0,0,0.23)}.menu-button:focus,.menu-button:hover{background:#bcbcbc}

.audio{position:fixed;bottom:10px;left:10px;opacity:0.5}.audio .menu-button{width:60px;min-width:0;padding:1rem}

.mobile-controls{display:none;opacity:0.5;position:fixed;bottom:20px;right:20px}.mobile-controls__up-container,.mobile-controls__down-container{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center}.mobile-controls .menu-button{width:60px;min-width:0;padding:1rem}@media (max-width: 1024px){.mobile-controls{display:block}}

.snake-game{width:100%;height:100%;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.snake-game__board{-webkit-flex-grow:1;flex-grow:1;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;overflow:hidden}.snake-game__svg rect.wall{fill:#307916}

.card{padding:20px 10px;margin:10px;position:relative;display:-webkit-inline-flex;display:inline-flex;-webkit-flex-direction:column;flex-direction:column;background:#307916;z-index:0;box-shadow:0 3px 6px rgba(0,0,0,0.16),0 3px 6px rgba(0,0,0,0.23)}.card::before,.card::after{content:'';display:block;position:absolute;background:#307916;z-index:-1}.card::before{top:10px;bottom:10px;left:-10px;right:-10px}.card::after{top:4px;bottom:4px;left:-6px;right:-6px}

.menu-link{position:relative;display:inline-block;background:#7c7c7c;color:white;border-bottom:6px inset rgba(0,0,0,0.5);border-left:6px inset rgba(0,0,0,0.5);border-right:6px inset rgba(255,255,255,0.5);border-top:6px inset rgba(255,255,255,0.5);margin:1rem;min-width:200px;padding:1rem 4.6rem;cursor:pointer;font-size:2.4rem;text-decoration:none;text-align:center;text-transform:uppercase;box-shadow:0 3px 6px rgba(0,0,0,0.16),0 3px 6px rgba(0,0,0,0.23)}.menu-link:focus,.menu-link:hover{background:#bcbcbc}.menu-link--primary{background:#005800}.menu-link--primary:focus,.menu-link--primary:hover{background:#006800}.menu-link--secondary{background:#0000bc}.menu-link--secondary:focus,.menu-link--secondary:hover{background:#0000fc}

.game-over{position:fixed;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.game-over__title{font-size:4rem;color:white;text-align:center;margin:15px}

.game{height:100%;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.game__board{-webkit-flex-grow:1;flex-grow:1;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;overflow:hidden}

.paused{position:fixed;width:100%;height:100%}.paused__card{position:fixed;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.paused__backdrop{width:100%;height:100%;background-color:black;opacity:0.2}.paused__title{font-size:4rem;color:white;text-align:center;margin:15px}

.score{margin:5px 20px;color:white;font-size:2.4rem;text-align:center}

.demo-snake-game{position:absolute;width:100%;height:100%;top:0;left:0}.demo-snake-game__board{height:100%}

.high-score{height:100%;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.high-score .card{background-color:black;-webkit-animation-name:floating;animation-name:floating;-webkit-animation-duration:3s;animation-duration:3s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite;-webkit-animation-timing-function:ease-in-out;animation-timing-function:ease-in-out}.high-score .card::before,.high-score .card::after{background-color:black}.high-score h2{font-size:4rem;color:white;text-align:center;margin:15px}.high-score p{font-size:3rem;color:white;text-align:center;margin-bottom:15px}@-webkit-keyframes floating{0%{-webkit-transform:translate(0, 0px);transform:translate(0, 0px)}50%{-webkit-transform:translate(0, 10px);transform:translate(0, 10px)}100%{-webkit-transform:translate(0, 0px);transform:translate(0, 0px)}}@keyframes floating{0%{-webkit-transform:translate(0, 0px);transform:translate(0, 0px)}50%{-webkit-transform:translate(0, 10px);transform:translate(0, 10px)}100%{-webkit-transform:translate(0, 0px);transform:translate(0, 0px)}}

.home-menu__title{font-size:4rem;color:white;text-align:center;margin:15px}

.home{height:100%;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.home__menu{-webkit-animation-name:floating;animation-name:floating;-webkit-animation-duration:3s;animation-duration:3s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite;-webkit-animation-timing-function:ease-in-out;animation-timing-function:ease-in-out}@-webkit-keyframes floating{0%{-webkit-transform:translate(0, 0px);transform:translate(0, 0px)}50%{-webkit-transform:translate(0, 10px);transform:translate(0, 10px)}100%{-webkit-transform:translate(0, 0px);transform:translate(0, 0px)}}@keyframes floating{0%{-webkit-transform:translate(0, 0px);transform:translate(0, 0px)}50%{-webkit-transform:translate(0, 10px);transform:translate(0, 10px)}100%{-webkit-transform:translate(0, 0px);transform:translate(0, 0px)}}

html,
button {
  font-family: 'VT323', monospace;
  font-size: 62.5%;
}

html,
body {
  height: 100%;
  margin: 0;
  background-color: #aced96;
}

body {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

#root {
  width: 100%;
  height: 100%;
}

