@import '../../assets/mixins.scss';

$color: #307916;

.card {
  padding: 20px 10px;
  margin: 10px;

  position: relative;

  display: inline-flex;
  flex-direction: column;

  background: $color;

  z-index: 0;

  @include shadow;

  &::before,
  &::after {
    content: '';
    display: block;
    position: absolute;
    background: $color;
    z-index: -1;
  }

  &::before {
    top: 10px;
    bottom: 10px;
    left: -10px;
    right: -10px;
  }

  &::after {
    top: 4px;
    bottom: 4px;
    left: -6px;
    right: -6px;
  }
}
