@import '../../assets/mixins.scss';

.high-score {
  height: 100%;

  display: flex;

  justify-content: center;
  align-items: center;

  .card {
    background-color: black;
    &::before,
    &::after {
      background-color: black;
    }

    animation-name: floating;
    animation-duration: 3s;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;
  }

  h2 {
    font-size: 4rem;

    color: white;

    text-align: center;

    margin: 15px;
  }

  p {
    font-size: 3rem;

    color: white;

    text-align: center;

    margin-bottom: 15px;
  }
}
@include floating;
