@import url(https://fonts.googleapis.com/css?family=VT323);

html,
button {
  font-family: 'VT323', monospace;
  font-size: 62.5%;
}

html,
body {
  height: 100%;
  margin: 0;
  background-color: #aced96;
}

body {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

#root {
  width: 100%;
  height: 100%;
}
