.mobile-controls {
  display: none;

  opacity: 0.5;

  position: fixed;
  bottom: 20px;
  right: 20px;

  &__up-container,
  &__down-container {
    display: flex;
    justify-content: center;
  }

  .menu-button {
    width: 60px;
    min-width: 0;
    padding: 1rem;
  }

  @media (max-width: 1024px) {
    display: block;
  }
}
